.acoommodationList {
    @extend .flex-container;
}

.sortByCategory {
  display: flex;
  margin: 10px 0px 30px 0px;

    .sortByCategory__button,
    .sortByCategory__button-active {
        display: flex;
        flex-direction: column; 
        align-items: center;
        border: none;
        margin-right: 20px;
        padding: 10px 20px;
        background-color: white;
        
        @media (max-width: $tablet-min-width) {
            margin-right: 8px;
            padding: 5px;
        }
        
        &:hover {
            cursor: pointer;
            background-color: lighten($color: $color-active-link, $amount: 40);
        } 
    } 

    .sortByCategory__button-active {
        background-color: lighten($color: $color-active-link, $amount: 40);
    }

    .sortByCategory__icon {
        font-size: 3em;
    }
}

.searchAccommodation {
    background-color: $color-dark-background;
    box-shadow: 6px 6px 6px $color-dark-background;
    padding: 15px 10px;
    margin: 10px 0px 30px 0px;
    border: none;
    width: 300px;

    @media (min-width: $tablet-min-width) {
        width: 400px;
    }

    &:focus {
        outline: none;
        background-color: $color-light-background;
        transition: 1s all;
    }
}